import React, {useState, useEffect} from "react";
import {useParams, Link} from "react-router-dom";

import {Grid, Fab, Box, styled} from "@mui/material";

import {Refresh as RefreshIcon, Home as HomeIcon} from "@mui/icons-material";

import {firestore} from "../../firebase";


import {ReactComponent as ErrorIllustration} from "../../illustrations/error.svg";
import {ReactComponent as NoDataIllustration} from "../../illustrations/no-data.svg";
import EmptyState from "../../domain/EmptyState";
import Loader from "../../components/Loader";
import UserCard from "../../components/UserCard";


const ProfileGrid = styled(Grid)(({theme}) => ({
    margin: 0,
    width: "100%"
}));

const UserPage = () => {
    const [loading, setLoading] = useState(true);
    const [user, setUser] = useState(null);
    const [error, setError] = useState(null);
    const {userId} = useParams();

    useEffect(() => {
        return firestore
            .collection("users")
            .doc(userId)
            .onSnapshot(
                (snapshot) => {
                    setLoading(false);
                    setUser(snapshot.data());
                },
                (error) => {
                    setLoading(false);
                    setError(error);
                }
            );
    }, [userId]);

    if (error) {
        return (
            <EmptyState
                image={<ErrorIllustration/>}
                title="Couldnt retrieve user."
                description="Something went wrong when trying to retrieve the requested user."
                button={
                    <Fab
                        variant="extended"
                        color="primary"
                        onClick={() => window.location.reload()}
                    >
                        <Box clone mr={1}>
                            <RefreshIcon/>
                        </Box>
                        Retry
                    </Fab>
                }
            />
        );
    }

    if (loading) {
        return <Loader/>;
    }

    if (!user) {
        return (
            <EmptyState
                image={<NoDataIllustration/>}
                title="User doesnt exist."
                description="The requested user doesnt exist."
                button={
                    <Fab variant="extended" color="primary" component={Link} to="/">
                        <Box clone mr={1}>
                            <HomeIcon/>
                        </Box>
                        Home
                    </Fab>
                }
            />
        );
    }

    const hasProfile = user.firstName && user.lastName && user.username;

    if (hasProfile) {
        return (
            <ProfileGrid container justifyContent="center" spacing={5}>
                <Grid item xs={4}>
                    <UserCard user={user}/>
                </Grid>
            </ProfileGrid>
        );
    }

    return (
        <EmptyState
            image={<NoDataIllustration/>}
            title="No profile."
            description="The user hasnt setup their profile."
        />
    );
}

export default UserPage;
